/*3.1 wpo-hero-slider*/

.wpo-hero-section-1{
	position: relative;
	height: 900px;
	background: #edf2f8;
	display: flex;
	justify-content: center;
	flex-direction: column;
	z-index: 1;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		height: 100%;
		width: 67%;
		background: #edf2f8;
		-webkit-clip-path: polygon(0 0, 75% 0, 90% 100%, 0 100%);
		clip-path: polygon(0 0, 75% 0, 90% 100%, 0 100%);
	}
	
	.right-img{
	  position: absolute;
	  right: 0;
	  top: 0;
	  height: 900px;
	  width: 50%;
	  z-index: -11;
	  background: url(../../images/slider/slide-1.jpg) no-repeat right center;

	  @media(max-width:1440px){
		top: -30px;
	  }
	  @media(max-width:1200px){
		top: 0px;
	  }
	}
	
	.right-img2 {
		position: absolute;
		left: 60px;
		bottom: -1px;
		z-index: 1;
	}
	.right-img img{
	  height: 100%;
	  width: 100%;
	}
  
	.wpo-hero-title h2{
	  font-size: 70px;
	  font-weight: 900;
	  line-height: 90px;
	  margin:10px 0 25px;
	}
	.wpo-hero-title h2 span{
	  color: $theme-primary-color;
	}

	.wpo-hero-title-top{
		span{
			font-size: 22px;
			color: $light;
			font-family: $heading-font;
		}
	}

	.wpo-hero-subtitle{
		p{
			font-size: 25px;
			color: $text-light-color;
			font-family: $heading-font;
			line-height: 35px;
			max-width: 595px;
			padding-left: 20px;
			border-left: 2px solid $text-light-color;
			margin-bottom: 60px;
		}
	}
  }



.wpo-hero-section-text{
	position: relative;

	.video-holder .video-btn{
		button.wrap{
			position: absolute;
			right:-80px;
			top: 50%;
			transform: translate(-50%, -50%);
			display: block;
			width: 84px;
			height: 84px;
			background: $white;
			text-align: center;
			line-height: 90px;
			color: $theme-primary-color;
			font-size: 30px;
			border-radius: 50%;
			border: 0;

			&:before{
				content: " ";
				width: 130%;
				height: 130%;
				background:rgba($color: #fff, $alpha: 0.3);
				border-radius: 50%;
				position: absolute;
				left: -12px;
				top: -12px;
				-webkit-animation: pulse 1s infinite;
				animation: pulse 1s infinite;
				z-index: -1;
			}

			.fi{
				font-size: 25px;
			}
		}

	}
}

@-webkit-keyframes pulse {
	from {
	  -webkit-transform: scale3d(1, 1, 1);
	  transform: scale3d(1, 1, 1);
	}
  
	50% {
	  -webkit-transform: scale3d(1.05, 1.05, 1.05);
	  transform: scale3d(1.05, 1.05, 1.05);
	}
  
	to {
	  -webkit-transform: scale3d(1, 1, 1);
	  transform: scale3d(1, 1, 1);
	}
  }
  
  @keyframes pulse {
	from {
	  -webkit-transform: scale3d(1, 1, 1);
	  transform: scale3d(1, 1, 1);
	}
  
	50% {
	  -webkit-transform: scale3d(1.05, 1.05, 1.05);
	  transform: scale3d(1.05, 1.05, 1.05);
	}
  
	to {
	  -webkit-transform: scale3d(1, 1, 1);
	  transform: scale3d(1, 1, 1);
	}
  }




/*--------------------------------------------------------------
hero slider s2
--------------------------------------------------------------*/

.wpo-hero-section-1.wpo-hero-section-2{
	background: $white;
	height: 900px;

	@media(max-width:991px){
		background: #edf2f8;
	 }

	&:before{
		display: none;
	}

	.right-vec{
		z-index: 1;
		.right-img{
			position: absolute;
			z-index: -11;
			background: none;

			@media(max-width:991px){
               display: block;
			   position: relative;
			   height: unset;
			}

			.r-img{
				position: absolute;
				right: 60px;
				top: 50%;
				transform: translateY(-50%);
				img{
					@media(max-width:991px){
						display: none;
					 }
				}
			}

			img{
				width: 100%;
				height: unset;
			}
	
			.wpo-happy-client{
				position: absolute;
				left: -50px;
    			bottom: 20%;
				padding: 45px 30px;
				background: $white;
				border-radius: 20px;
