					list-style: none;
					display: flex;

					.owl-nav{
						display: none;
					}
	
					li{
						width: 55px;
						height: 55px;

						img{
							width: 55px;
							height: 55px;
							@include rounded-border(50%);
						}
					}
				}
			}
			  
		}

		.svg-shape{
			position: absolute;
			bottom: -10%;
			width: 100%;
			right: 100px;
			z-index: -111;
			svg{
				width: 100%;
			}

			@media(max-width:1440px){
				bottom: -20%;
			}
		}

	}


	.video-holder {
		button.wrap {
			position: absolute;
			right:20%;
			top: 50%;
			transform: translate(-50%, -50%);
			z-index: 999;
			display: block;
			width: 84px;
			height: 84px;
			background: $white;
			text-align: center;
			line-height: 90px;
			color: $theme-primary-color;
			font-size: 30px;
			border-radius: 50%;
			border: 0;

			&:before{
				content: " ";
				width: 130%;
				height: 130%;
				background:rgba($color: #fff, $alpha: 0.3);
				border-radius: 50%;
				position: absolute;
				left: -12px;
				top: -12px;
				-webkit-animation: pulse 1s infinite;
				animation: pulse 1s infinite;
				z-index: -1;
			}

			.fi{
				font-size: 25px;
			}
		}

	}

	  
}


.wpo-hero-slider {
	width: 100%;
	height: 900px;
	display: flex;
	position: relative;
	z-index: 0;

	@include media-query(991px) {
		height: 600px;
	}

	@include media-query(767px) {
		height: 500px;
	}

	.hero-slide{
		height: 900px;
		
		@include media-query(991px) {
			height: 600px;
		}
	
		@include media-query(767px) {
			height: 500px;
		}
	}

	.hero-container {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	.slide-inner{
		width: 100%;
		height: 100%;
		z-index: 1;
		background-size: cover;
		background-position: center;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: left;

		.slide-content{
			padding-bottom: 70px;

			@media(max-width:991px){
              padding-bottom: 0;
			}
		}
	}
	.slick-prev,
	.slick-next {
		background-color: transparentize($theme-primary-color, 0.3);
		width:45px;
		height: 45px;
		z-index: 10;
		@include rounded-border(50%);
		opacity: 0;
		visibility: hidden;
		transition: all .3s;
		border: 2px solid $theme-primary-color;

		&:hover{
			background-color: $theme-primary-color;
		}
	}

	.slick-prev {
		left: 0px;

		@include media-query(767px) {
			display: none !important;
		}

		&:before {
			font-family: "themify";
			content: "\e629";
			opacity: 1;
		}
	}

	.slick-next {
		right: 0px;

		@include media-query(767px) {
			display: none !important;
		}

		&:before {
			font-family: "themify";
			content: "\e628";
			opacity: 1;
		}
	}

	&:hover{

		.slick-next {
			right: 20px;
			opacity: 1;
			visibility: visible;
		}

		.slick-prev {
			left: 20px;
			opacity: 1;
			visibility: visible;
		}
	}


}


.wpo-hero-slider {

	@include media-query(1199px) {
		height: 680px;
	}

	@include media-query(991px) {
		height: 600px;
	}

	@include media-query(767px) {
		height: 500px;
	}


	.gradient-overlay {
		@include linear-gradient-bg(left, $dark-gray 45%, transparent 65%);
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 10;
		opacity: .6;
	}

	.gradient-overlay + .container {
		position: relative;
		z-index: 11;
	}

	.swiper-slide {
		position: relative;
		z-index: 11;
	}

	.wpo-hero-title-top{
		span{
			font-size: 22px;
			color: #e4e4e4;
			font-family: $heading-font;

			@include media-query(767px) {
				font-size: 15px;
			}
	
		}
	}

	.slide-title {
		max-width: 655px;

		@include media-query(1199px) {
			max-width: 555px;
		}


		h2 {
			font-size: 70px;
			font-weight: 900;
			line-height: 90px;
			margin:10px 0 25px;
			color: $white;

			@include media-query(1199px) {
				font-size: 50px;
				font-size: calc-rem-value(50);
			}

			@include media-query(991px) {
				font-size: 40px;
				font-size: calc-rem-value(40);
				line-height: 55px;
			}

			@include media-query(767px) {
				font-size: 30px;
				font-size: calc-rem-value(30);
				line-height: 36px;
			}
		}
	}

	.slide-text {
		max-width: 520px;

		@include media-query(767px) {
			max-width: 500px;
		}
		
		p {
			font-size: 25px;
			color: $text-light-color;
			font-family: $heading-font;
			line-height: 35px;
			max-width: 595px;
			padding-left: 20px;
			border-left: 2px solid #e2e2e2;
			margin-bottom: 60px;
			color: #e2e2e2;

			@include media-query(991px) {
				font-size: 18px;
				font-size: calc-rem-value(18);
			}

			@include media-query(767px) {
				font-size: 16px;
				font-size: calc-rem-value(16);
				line-height: 22px;
				margin-bottom: 30px;
			}
		}		
	}

	.slide-btns {
		@include media-query(991px) {
			max-width: 400px;
		}
	}


	.slide-btns .hero-video-btn {
		margin-left: 40px;

		@include media-query(767px) {
			margin-left: 20px;
		}
	}
}



